import React, { Suspense } from "react";
import classes from "./AllRoutes.module.css";
import { Redirect, Route, Switch } from "react-router-dom";
const About = React.lazy(() => import("../Pages/About"));
const Cart = React.lazy(() => import("../Pages/Cart"));
const Category = React.lazy(() => import("../Pages/Category"));
const Contact = React.lazy(() => import("../Pages/Contact"));
const Product = React.lazy(() => import("../Pages/Product"));
const ProductDetail = React.lazy(() => import("../Pages/ProductDetail"));
const MainContent = React.lazy(() => import("../HomePage/MainContent"));

const AllRoutes = () => {
  return (
    <Suspense fallback={<div className={classes.loading}></div>}>
      <Switch>
        <Route path="/E-Commerce/about">
          <About />
        </Route>
        <Route path="/E-Commerce/contact">
          <Contact />
        </Route>
        <Route path="/E-Commerce/category">
          <Category />
        </Route>
        <Route path="/E-Commerce/product" exact>
          <Product />
        </Route>
        <Route path="/E-Commerce/product/:id">
          <ProductDetail />
        </Route>
        <Route path="/E-Commerce/home">
          <MainContent />
        </Route>
        <Route path="/E-Commerce/cart">
          <Cart />
        </Route>
        <Route path="/E-Commerce" exact>
          <MainContent />
        </Route>
        <Route path="/" exact>
          <Redirect to="/home" />
        </Route>
        <Route path="*">Item Not Found</Route>
      </Switch>
    </Suspense>
  );
};

export default AllRoutes;
